import tuiListCell from "@/components/list-cell/list-cell"
import tuiButton from "@/components/extend/button/button"
export default {
	components: {
		tuiListCell,
		tuiButton
	},
	data() {
		return {

		}
	},
	methods: {
		href(page) {
			let url = "";
			switch (page) {
				case 1:
					url = "../userInfo/userInfo"
					break;
				case 2:
					url = "../address/address"
					break;
				default:
					break;
			}
			uni.navigateTo({
				url: url
			})
		}
	}
}
